// ** React Import
import React, { useEffect, useRef, useState } from 'react'

// ** Custom Components
import Sidebar from '@components/sidebar'
import InputGroup from '@components/input-group'

// ** Utils
// ** Third Party Components
import classnames from 'classnames'
import { Button, Form } from 'reactstrap'

// ** Store & Actions
import { addRecoveryCategory, recoveryCategoryFormChange } from '@store/RecoveryCategory/actions'
import { useDispatch, useSelector } from 'react-redux'

const SidebarNewRecoveryCategories = ({
  open,
  toggleSidebar
}) => {
  const formEl = useRef(null)
  const [isOpened, setIsOpened] = useState(false)
  const { recoveryCategoryFormData, formErrors, isLoading, isSubmitting } = useSelector(state => state.recoveryCategories),
    dispatch = useDispatch()

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(addRecoveryCategory())
  }

  const handleFormChange = (name, value) => {
    dispatch(recoveryCategoryFormChange(name, value))
  }

  const onOpened = () => {
    setIsOpened(true)
  }

  return (
    <Sidebar
      onOpened={onOpened}
      size='lg'
      open={open}
      title='New RecoveryCategory'
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit} innerRef={formEl}>
        <InputGroup
          type={'text'}
          label={'Service Name'}
          error={formErrors['serviceName']}
          name={'serviceName'}
          placeholder={'Service Name'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={recoveryCategoryFormData.serviceName}
          required={true}
          className={classnames({ 'is-invalid': formErrors['serviceName'] })}
        />
        <InputGroup
          type={'text'}
          label={'Service Category'}
          error={formErrors['serviceCategory']}
          name={'serviceCategory'}
          placeholder={'Service Category'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={recoveryCategoryFormData.serviceCategory}
          required={true}
          className={classnames({ 'is-invalid': formErrors['serviceCategory'] })}
        />
        <InputGroup
          type={'text'}
          label={'Service SubCategory'}
          error={formErrors['serviceSubCategory']}
          name={'serviceSubCategory'}
          placeholder={'Service SubCategory'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={recoveryCategoryFormData.serviceSubCategory}
          required={true}
          className={classnames({ 'is-invalid': formErrors['serviceSubCategory'] })}
        />
        <InputGroup
          type={'text'}
          label={'Service Icon Url'}
          error={formErrors['serviceIconUrl']}
          name={'serviceIconUrl'}
          placeholder={'Service Icon Url'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={recoveryCategoryFormData.serviceIconUrl}
          required={true}
          className={classnames({ 'is-invalid': formErrors['serviceIconUrl'] })}
        />
        <InputGroup
          type={'number'}
          label={'Base Price'}
          error={formErrors['basePrice']}
          name={'basePrice'}
          placeholder={'Base Price'}
          onInputChange={(name, value) => {
            handleFormChange(name, value)
          }}
          defaultValue={recoveryCategoryFormData.basePrice}
          required={true}
          className={classnames({ 'is-invalid': formErrors['basePrice'] })}
        />
        <Button type='submit' className='mr-1' color='primary'>
          Submit
        </Button>
        <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
          Cancel
        </Button>
      </Form>
    </Sidebar>
  )
}

export default SidebarNewRecoveryCategories
