// ** React Imports
// ** Custom Components
import UserListAvatar from '@components/user-avatar/UserListAvatar'
import TableAction from '@components/table/TableAction'

// ** Store & Actions
import { getRecoveryCategory } from '@store/RecoveryCategory/actions'
import { store } from '@store/storeConfig/store'

// ** Third Party Components
import { Archive, FileText } from 'react-feather'

const statusObj = {
  pending: 'light-warning',
  active: 'light-success',
  inactive: 'light-secondary'
}

export const columns = [
  {
    name: 'Service Name',
    minWidth: '297px',
    selector: 'serviceName',
    sortable: true,
    cell: row => {
      return (
        <UserListAvatar
          name={row.serviceName}
          photo={''}
          viewHandler={() => store.dispatch(getRecoveryCategory(row._id))}
          viewLink={`/recoveryCategory/view/${row._id}`}
        />
      )
    }
  },
  {
    name: 'Service Category',
    minWidth: '229px',
    selector: 'serviceCategory',
    sortable: true,
    cell: row => <span className='text-capitalize'>{row.serviceCategory}</span>
  },
  {
    name: 'Base Price',
    minWidth: '172px',
    selector: 'basePrice',
    sortable: true,
    cell: row => <span className='text-capitalize'>{row.basePrice}</span>
  },
  {
    name: 'Actions',
    minWidth: '100px',
    cell: row => {
      const actions = [
        {
          link: `/recoveryCategory/view/${row._id}`,
          handler: () => store.dispatch(getRecoveryCategory(row._id)),
          label: 'Details',
          icon: (<FileText size={14} className='mr-50' />)
        },
        {
          link: `/recoveryCategory/edit/${row._id}`,
          handler: () => store.dispatch(getRecoveryCategory(row._id)),
          label: 'Edit',
          icon: (<Archive size={14} className='mr-50' />)
        }
      ]
      return (
        <TableAction actions={actions} />
      )
    }
  }
]
