import { useState } from "react"
import { useSelector } from 'react-redux'

// ** Invoice List Sidebar
import Sidebar from './Sidebar'

// ** Columns
import { columns } from './columns'

// ** Store & Actions
import { getAllRecoveryCategories, getRecoveryCategoryList } from '@store/RecoveryCategory/actions'

// ** Custom Components
import CarhootTable from '@components/table/CarhootTable'

// ** Styles
import '@styles/react/libs/react-select/_react-select.scss'
import '@styles/react/libs/tables/react-dataTable-component.scss'
import '@styles/react/apps/app-users.scss'

const RecoveryCategoriesList = () => {
  // ** States
  const [sidebarOpen, setSidebarOpen] = useState(false)

  const { allData, data, total } = useSelector(state => state.recoveryCategories)

  // ** Function to toggle sidebar
  const toggleSidebar = () => setSidebarOpen(!sidebarOpen)

  const customFilters = []

  const searchParams = {}

  return (
    <div className='app-user-list'>
      <CarhootTable
        allData={allData}
        data={data}
        total={total}
        columns={columns}
        filters={customFilters}
        searchParams={searchParams}
        getList={getRecoveryCategoryList}
        getAllList={getAllRecoveryCategories}
        headerActionHandler={toggleSidebar}
        headerActionLabel={'Add New Recovery Category'}
      />
      <Sidebar open={sidebarOpen} toggleSidebar={toggleSidebar} />
    </div>
  )
}

export default RecoveryCategoriesList
